.gallery {
  background-image: linear-gradient(#fff5f6 20%, #fff 20%, #fff 80%);
}

.gallery .gallery__container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0;
}

.gallery .gallery__list {
  width: 100%;
  max-width: 100vh;
  grid-template-rows: [start] minmax(43px, 1fr)[row2] minmax(43px, 1fr)[row-end] minmax(43px, 1fr);
  grid-template-columns: repeat(auto-fit, minmax(43px, 1fr));
  gap: 2.53px;
  display: grid;
}

.gallery .gallery__item {
  border-radius: 8px;
  padding: 0 0 85%;
  position: relative;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .25);
}

.gallery .gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 320px) {
  .gallery__item:nth-child(1) {
    grid-row-start: row2;
  }

  .gallery__item:nth-child(2) {
    grid-row-start: row2;
    transform: translateY(60%);
  }

  .gallery__item:nth-child(3) {
    grid-column-start: 2;
    transform: translateY(60%);
  }

  .gallery__item:nth-child(4) {
    grid-area: span 2 / span 2;
  }

  .gallery__item:nth-child(5) {
    transform: translateY(70%);
  }

  .gallery__item:nth-child(6) {
    transform: translateY(40%);
  }

  .gallery__item:nth-child(7) {
    grid-row-start: row2;
    grid-column-start: 6;
    transform: translateY(40%);
  }

  .gallery__item:nth-child(8) {
    grid-row-start: row-end;
    grid-column-start: 3;
  }

  .gallery__item:nth-child(9) {
    transform: translateY(105%);
  }

  .gallery__item:nth-child(10) {
    transform: translateY(70%);
  }
}

.swiper {
  width: 100%;
}

.swiper-slide {
  text-align: center;
  background: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 0;
}

.thumb {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 32px;
  overflow: hidden;
  box-shadow: 0 20px 30px -10px #05f471;
}

.thumb__img {
  width: 100%;
  height: auto;
  object-fit: contain;
  object-position: center;
}

.swiper-pagination-bullet {
  opacity: .7;
  width: 25px;
  height: 25px;
  background-color: #c97c8e;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 50%;
}

.swiper-pagination-bullet:first-child {
  background-image: url("bgi-jul.d6b32ce7.png");
}

.swiper-pagination-bullet:nth-child(2) {
  background-image: url("bgi-max.7ab059d2.png");
}

.swiper-pagination-bullet:nth-child(3) {
  background-image: url("bgi-maks.de110aae.png");
}

.swiper-pagination-bullet:nth-child(4) {
  background-image: url("bgi-lavr.2881d4d2.png");
}

.swiper-pagination-bullet-active {
  height: 50px;
  width: 50px;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 50%;
  display: inline-flex;
}

